.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  place-content: space-between;
  
  background:black;
  position: absolute;
  height: 100vh;
  width: 100vw;
 z-index: 2;
 overflow: hidden;
 

}
body{background: black;padding: 0;margin: 0;overflow-x: hidden;}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header ,footer{
  
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  justify-items: center;
  align-content: center;
  color: white;
  gap: 2%;
  border-bottom: 0.1rem solid darkblue;
  width: 100%;
  background: rgba(0, 0, 0, 0.943);
  
  
  

}
.App-header h3,footer h3{display: flex;align-items: center;justify-content: center;align-content: center;}
.App-header img,footer img{border-radius: 50%;}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-header h1{display: flex;align-items: center;
justify-content: center;align-content: center;justify-items: center;}


button{
  background:#000f0f;
  font-weight: 900;
  padding: 2% 3%;
  color:rgb(243, 231, 231);
  border: 2px solid rgb(1, 11, 11);
  box-shadow: 0px 0px 1px 3px rgb(0, 0, 0);
  transition: 0.3s all;
  border-radius: 1rem;
  cursor: pointer;
}
button:hover,label[for="Play"]:hover{
  transform: scale(1.03,1.03) ;
  background: cyan;
  color: black;
  box-shadow: 0px 0px 11px 13px rgb(6, 47, 47);
  border: 2px solid cyan;

}
.Menu{
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  justify-items: center;
  gap: 5%;
  margin: 2%;
  width: 100vw;
  background:transparent;
 
}



.SpeedSet{
  background: rgba(5, 128, 128, 0.543);
  padding: 2%;
  position: absolute;
  height:50%;
  width: 50%;
  border: 3px solid cyan;
  border-radius: 0.5rem;
  box-shadow:0px 0px 11px 11px #011818;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: 0.3s all;
  animation: ShowOff 0.3s linear 1;
  overflow: hidden;
}

@keyframes ShowOff {
  from{opacity: 0;transform: scale(0.1,0.1);}
  to{opacity: 1;transform: scale(1.1,1.1);}
}
.Hidden{
  display: none;
}
input[type="range"]{
  appearance: none;
  background:rgb(0, 4, 4);
  border-radius: 1rem;
  box-shadow: 0px 0px 0.4rem 0.1rem cyan;
  color: aliceblue;
}
.SpeedSet::before{
  position: absolute;
  content: 'Adjust Speed';
  height: 10%;
  width: 100%;
  background: #011818;
 top: 0%;
 left: 0%;
 align-items: center;
 justify-content: center;
 display: flex;
 text-shadow: 0px 0px 0.72rem black;
 font-size: 1.5rem;
 transform: skew(10deg,0deg);
}
input[type="file"]{
  position: relative;
  display: none;

}

label[for="Play"]{
background: rgb(1, 10, 10);
padding: 2% 2%;
cursor: pointer;
font-size: 1.3rem; 
font-weight: 500;
border-radius: 1rem;
transition: 0.3s all;
}

/*for anchors*/
a{
  position: relative;
  color: aliceblue;
  text-decoration: none;
  background:darkblue;
  padding: 1%;
  border: 2px solid darkblue;
  border-radius: 0.3rem;
  box-shadow: inset 0 0 0.3rem 0.1rem black;
  transition: 0.3s linear all;
}
a:hover{
  box-shadow: 0 0 0.3rem 0.1rem darkblue ,0 0 0.3rem 0.1rem dodgerblue;
}


main{
  display:flex;
  width: 100%;

  

 }


/*side-Bar*/
.aside{
  width: 20%;
 
  border-right:0.1em solid black;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: scroll;
  padding: 0.3rem;
  color: aliceblue;
}
::-webkit-scrollbar{appearance: none;}
::-webkit-scrollbar-thumb{
  background:dodgerblue;
  border-radius: 1rem;
}
::-webkit-scrollbar-track{
  background: darkblue;
  border-radius: 1rem;
}
/*main-content*/
.mainContent{
display: flex;
flex-direction: column;
width: 80%;
height: calc(80vh);
place-content: start;
align-items: start;
overflow-x: hidden;
overflow-y: scroll;
color: aliceblue;
padding: 2%;
gap: 100px;

}
*{scroll-behavior: smooth;scrollbar-width: 10px;}
/*display*/
.display{
 
  color:aliceblue;
  position: relative;
  border-radius: 0.5em;
  padding: 0;
  
  
}
.display img{border: 0.2rem groove gainsboro;}
.display p{
  position: absolute;
  top: 80%;
  left: 25%;
  transform: translateY(-80%,-25%);
  width: 50%;
  background:rgba(1, 1, 56, 0.87);
  border: 0.2rem solid black;
  font-weight: bold;
}



/*container*/
.container{
  display: flex;
  flex-direction: column;
  place-content: flex-start;
  align-items: flex-start;
  text-align: left;


}

/*grid-container*/
.grid-container{
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
}

.text{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-align: left;


}

/*list*/
.List{
  display: flex;
  flex-direction: column;
  padding: 5px;
  gap: 20px;
}

.ListItem{
border-bottom: 2px solid white;
display: flex;
align-items: flex-start;
place-content: center;
text-align: left;
font-weight: bold;

}
.ListItem a{
  background: transparent;
  box-shadow: none;
  border: none;
  cursor: pointer;
  width: 100%;
}

/*vide0*/
video{width: 100%;}

/*removing style from div anchor*/
.grid-container a{background: transparent;text-decoration: underline;border: none;box-shadow: none;
color: dodgerblue;}